import { initI18n } from '@wix/fe-essentials/i18n';
import {
  ErrorHandler,
  ErrorHandlerParams,
} from '../error-handler/ErrorHandler';
import { ErrorHandlerAPI } from '@wix/error-handler-core/types/v2';

export interface CreateErrorHandlerMockParams
  extends Partial<ErrorHandlerParams> {
  language?: string;
}

export function createErrorHandlerMock(
  params: CreateErrorHandlerMockParams,
): ErrorHandlerAPI {
  return new ErrorHandler({
    biLoggerOrFactory: {
      report: async () => {},
    },
    createErrorMonitor: () => ({
      captureException: () => {},
    }),
    environment: {
      artifactId: 'ErrorHandlerMock-artifactId',
    },
    createI18n: (options) =>
      initI18n({
        ...options,
        locale: params.language || 'en',
      }),
    ...params,
  }).v2;
}
